import Notification from "../src/components/Notification";
class ToastService {
    constructor() {
      if (!toasts) toasts = this;
      this.toasts = [];
      return toasts;
    }
  
    addToast = (toast) => {
      if (this.toasts.length < 3) {
        this.toasts.push(toast);
      }
    };
  
    generateToast = (...options) => {
      this.addToast({...options})
    };
  
    getAllToasts = () => {
      return this.toasts
    }
  }
  const toast = new ToastService();

export default {
  title: "Notification",
  component: Notification,
};

